<template>
    <div class="reset">
    <b-container>
    <b-row class="mt-5 mx-auto">
      <b-col>
        <b-card v-if="waiting" header="Reset Password" header-bg-variant="prime" header-text-variant="cream" header-tag="h3">
          <b-form @submit="onSubmit" @reset="onReset" v-if="show">

           <b-input-group class="my-4" label="Email address:" label-for="exampleInput4">
              <b-input-group-prepend inline is-text>
                  <b-icon icon="envelope"></b-icon>
                </b-input-group-prepend>
                <b-form-input
                            type="email"
                            v-model="form.email"
                            required
                            placeholder="Enter email">
                </b-form-input>
            </b-input-group>

            <button class="buttonDiv bg-second text-cream px-3" style="width:140px" @click="requestToken()"> Request Token </button>

            <template v-if="tokenCheck == true">
            <b-input-group class="my-4" label="Token:" label-for="exampleInput4">
              <b-input-group-prepend inline is-text>
                  <b-icon icon="caret-right-fill"></b-icon>
                </b-input-group-prepend>
                <b-form-input
                            v-model="form.token"
                            required
                            placeholder="Token">
                </b-form-input>
                 <b-form-invalid-feedback :state="validToken">
                  You must enter the token sent to your email
                </b-form-invalid-feedback>
            </b-input-group>

            <b-input-group v-if="validToken" id="password" label="Password:" label-for="exampleInput2">
                <b-input-group-prepend inline is-text>
                  <b-icon icon="lock-fill"></b-icon>
                </b-input-group-prepend>
                <b-form-input id="exampleInput2"
                            type="password"
                            v-model="form.password"
                            placeholder="Password"
                            required>
                </b-form-input>
            </b-input-group>

            <b-input-group v-if="validToken" class="my-4" id="exampleInputGroup3" label="Confirm Password:" label-for="exampleInput3">
                <b-input-group-prepend inline is-text>
                  <b-icon icon="lock-fill"></b-icon>
                </b-input-group-prepend>
                <b-form-input id="exampleInput3"
                            type="password"
                            v-model="form.confirm"
                            placeholder="Password"
                            required>
                </b-form-input>
                <b-form-invalid-feedback :state="validPass">
                  You must enter the same password
                </b-form-invalid-feedback>
            </b-input-group>

            <div class="d-flex justify-content-between">
                <div>
                <button class="buttonDiv bg-second px-3" style="width:140px" type="submit"> <b-icon-forward variant="cream" font-scale="1.5"></b-icon-forward></button>
                &nbsp;&nbsp;
                <button class="buttonDiv bg-alert px-3" style="width:60px" type="reset"> <b-icon-x-circle variant="cream" font-scale="1.5"></b-icon-x-circle></button>
                </div>
            </div>
            </template>
          </b-form>

        </b-card>
        <div v-else align="center">
            <h4 class="text-prime"> Reseting Password </h4>
            <b-icon icon="three-dots" animation="cylon" variant="prime" font-scale="6"></b-icon>
        </div>
      </b-col>
    </b-row>
  </b-container>

   <b-modal align="center" ref="success" hide-footer title="Reset Complete">
      <div class="d-block">
        <h3> {{msg}} </h3>
      </div>
      <button class="buttonDiv mt-3 bg-safe text-cream" style="width:60%"  @click="hideModal('success')">Close</button>
    </b-modal>

   <b-modal align="center" ref="fail" hide-footer title="Problem Found">
      <div class="d-block">
        <h3> {{msg}} </h3>
      </div>
      <button class="buttonDiv mt-3 bg-alert text-cream" style="width:60%"  @click="hideModal('fail')">Close</button>
    </b-modal>

  </div>

</template>

<script>
import router from '../router/index'

export default {
  name: 'app',
  data () {
    return {
      form: {
        email: '',
        username: '',
        token: '',
        password: '',
        confirm: ''
      },
      show: true,
      waiting: true,
      msg: null,
      tokenCheck: false,
      router: router
    }
  },
  computed: {
    validToken () {
      console.log(this.form.token.trim().length)
      return this.form.token.trim().length === 32
    },
    validPass () {
      return this.form.password === this.form.confirm
    }
  },
  methods: {
    showModal (msg) {
      this.msg = msg
      this.$refs['success'].show()
    },
    showAlert (msg) {
      this.msg = msg
      this.$refs['fail'].show()
    },
    hideModal (mode) {
      if (mode === 'success') {
        this.$refs['success'].hide()
      } else {
        this.$refs['fail'].hide()
        this.msg = null
        this.waiting = true
      }
    },
    onSubmit (evt) {
      console.log(evt)
      evt.preventDefault()
      if (this.validPass && this.validToken) {
        this.changePassword()
      } else {
        alert('Password or token is incorrect')
      }
    },
    onReset (evt) {
      evt.preventDefault()
      /* Reset our form values */
      for (let item in this.form) {
        this.form[item] = ''
      }
      /* Trick to reset/clear native browser form validation state */
      this.show = false
      this.$nextTick(() => { this.show = true })
    },
    requestToken () {
      alert('token sent to email')
      let _this = this
      this.$store.dispatch('requestToken', { email: this.form.email })
        .then(function (response) {
          if (!response.status) {
            _this.showAlert(response.msg)
            localStorage.setItem('tokenReady', false)
          } else {
            localStorage.setItem('tokenReady', true)
            localStorage.setItem('floatEmail', this.form.email)
            _this.showModal(response.msg)
          }
        })
    },
    changePassword () {
      this.waiting = false
      let _this = this
      this.$store.dispatch('changePassword', { userData: this.form })
        .then(function (response) {
          if (!response.status) {
            _this.showAlert(response.msg)
          } else if (response.status === 1) {
            _this.form.token = ''
            _this.form.password = ''
            _this.form.confirm = ''
            localStorage.tokenReady = false
          } else {
            _this.showModal(response.msg)
            _this.$router.push('/login')
          }
        })
    }
  },
  created () {
    if (localStorage.floatEmail) {
      this.form.email = localStorage.floatEmail
    }
    if (localStorage.tokenReady) {
      this.tokenCheck = true
    }
  }
}
</script>



